}

.game[data-v-d02ff2b0] {
  width: 100%;
  height: 100%;
  display: block;
  background: black;
  opacity: 1;
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.round[data-v-d02ff2b0] {
  background: aliceblue;
  border-radius: 9999px;
  position: absolute;
  -webkit-transition: width 2s, height 2s, margin 0.5s;
  transition: width 2s, height 2s, margin 0.5s
}
.cube[data-v-d02ff2b0] {
  background: salmon;
  position: absolute;
  -webkit-transition: width 2s, height 2s, margin 0.5s;
  transition: width 2s, height 2s, margin 0.5s
}
.bonus[data-v-d02ff2b0] {
