    font-size: 15px;
}
.popup-prk-score-content[data-v-d02ff2b0] {
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding: 10px 10px 10px 10px;
    width: 60%;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    -webkit-animation-name: animatetop-data-v-d02ff2b0;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop-data-v-d02ff2b0;
    animation-duration: 0.4s;
    opacity: 0.9;
@media (max-width: 500px) {
        width: 90%;
}
}
.popup-prk-score-head[data-v-d02ff2b0] {
    padding: 2px 16px;
    background: white;
    opacity: 0.8;
    text-align: center;
}
/* add animation effects */
@-webkit-keyframes animatetop-data-v-d02ff2b0 {
from {top:-300px; opacity:0
}
to {top:0; opacity:1
}
}
@keyframes animatetop-data-v-d02ff2b0 {
from {top:-300px; opacity:0
}
to {top:0; opacity:1
}
}
/* close button style */
.close-score[data-v-d02ff2b0] {
    color: lightgrey;
